import React from 'react';
import u1formuleIcon from '../../img/u1formule-icon.png';
import fiIcon from '../../img/fi-icon.png';
import bigFormuleWithFiIcon from '../../img/bigformulewithfi-icon.png';
import niHatchFormuleIcon from '../../img/nihatchformule-icon.png';
import Table from '../table';
import classes from './part-1.module.scss';
import Letters from '../../letters';
import graphImage4 from '../../img/graph_4_rgr2.jpg';
import ArithMeanEstimate from '../arith-mean-estimate';
import DispersionEstimate from '../dispersion-estimate';
import { calcValues } from './calc-values';

type InputType =
  | {
      title: string;
      xi1: number;
      hValue: number;
      arrN: number[];
      specX: number;
      sko: number;
      significanceLevel: number;
      xPirson: number;
      tValue: number;
    }
  | undefined
  | null;

type Props = {
  input: InputType;
  variantIndex: number;
};

const Part1 = ({ input, variantIndex }: Props) => {
  if (input === undefined || input === null) {
    return null;
  }

  const values = calcValues(variantIndex);

  if (values === null) {
    return null;
  }

  let currentGraph = '';

  if (variantIndex === 3) {
    currentGraph = graphImage4;
  }

  const {
    nValue,
    arrXi,
    arrEqualizationFrequencies,
    x0SqrArr,
    x0SqrValue,
    numberOfFreedom,
    generalDeviation,
    confidenceLevel,
    skoSqr,
    generalDispersionDeviation,
    hypotAccept,
  } = values;

  return (
    <div className={classes.wrapper}>
      <h3> I.  .  {variantIndex + 1}</h3>
      <h3>{input.title}</h3>
      <h3>{`${Letters.specX} = ${input.specX}; ${Letters.sko} = ${input.sko}; h = ${input.hValue}`}</h3>
      <Table
        tableArr={[
          [Letters.xi, ...arrXi.map((el) => el.toFixed(1))],
          [Letters.ni, ...input.arrN.map((el) => `${el}`)],
        ]}
      />
      <p>n = {nValue}</p>
      <ol>
        <li>
               
          <ol>
            <li> -.</li>
            H0:       
            <li>  .</li>
            <Table
              tableArr={[
                [
                  'i',
                  `${Letters.xi}`,
                  `${Letters.xi} - ${Letters.specX}`,
                  <img key="img1" src={u1formuleIcon} alt="" />,
                  <img key="img2" src={fiIcon} alt="" />,
                  <img key="img3" src={bigFormuleWithFiIcon} alt="" />,
                ],
                ...arrEqualizationFrequencies,
              ]}
            />
            <li>    {Letters.x0sqr}</li>
            <Table
              tableArr={[
                [
                  'i',
                  Letters.ni,
                  Letters.niHatch,
                  `${Letters.ni} - ${Letters.niHatch}`,
                  Letters.niMinusNiHatchSqr,
                  <img key="img4" src={niHatchFormuleIcon} alt="" />,
                ],
                ...x0SqrArr,
              ]}
            />
            <p>
              {Letters.x0sqr} = <span>{`${Letters.sum} `}</span>
              <img src={niHatchFormuleIcon} alt="" /> = {x0SqrValue}
            </p>
            <li>    v = n - 3</li>
            <span>{`v = ${input.arrN.length} - 3 = ${numberOfFreedom}`}</span>
            <li>     {Letters.xSqr}</li>
            <p>
              {`   ${Letters.significanceLevel} = ${input.significanceLevel}  
                 v = ${numberOfFreedom}  `}
              <span>{Letters.xSqr} = </span>
              {`(${input.significanceLevel}; ${numberOfFreedom}) = ${input.xPirson}`}
            </p>
            <li>
               :     {Letters.x0sqr} 
               {Letters.xSqr}.
            </li>
            <p>
              {Letters.x0sqr} {hypotAccept ? <span>&lt;</span> : <span>&gt;</span>} {Letters.xSqr}(
              {`${x0SqrValue} ${hypotAccept ? '<' : '>'} ${input.xPirson}`})
            </p>
          </ol>
        </li>
        <li>  .</li>
             OX    {Letters.xi},    OY -
           {Letters.niHatch}.
        <div>
          <img src={currentGraph} alt="" />
        </div>
        <li>    .</li>
        <p>
           n = {nValue}, {Letters.specX} = {input.specX}, {Letters.sko} = {input.sko}.
        </p>
        {/* Letters.infinity -  */}
        <p> n &gt; 30,  v = {Letters.infinity}.</p>
        <p>
          {/* Letters.infinity -  */}
           {Letters.significanceLevel} = {input.significanceLevel}  v = {Letters.infinity} -
              t({input.significanceLevel}; {Letters.infinity}) ={' '}
          {input.tValue}. 
        </p>
        <ArithMeanEstimate
          specX={input.specX}
          tValue={input.tValue}
          nValue={nValue}
          sko={input.sko}
          generalDeviation={generalDeviation}
        />
        <ArithMeanEstimate
          modifier="small"
          specX={input.specX}
          tValue={input.tValue}
          nValue={nValue}
          sko={input.sko}
          generalDeviation={generalDeviation}
        />
        <ArithMeanEstimate
          modifier="very small"
          specX={input.specX}
          tValue={input.tValue}
          nValue={nValue}
          sko={input.sko}
          generalDeviation={generalDeviation}
        />
        <li>   . </li>
        {/* Letters.infinity -  */}
        <p>
           n = {nValue}, {Letters.sko} = {input.sko}, v = {Letters.infinity}.
        </p>
        <p>
          {/* Letters.infinity -  */}
           {Letters.significanceLevel} = {input.significanceLevel}  v = {Letters.infinity} -
              t({input.significanceLevel}; {Letters.infinity}) ={' '}
          {input.tValue}. 
        </p>
        <DispersionEstimate
          tValue={input.tValue}
          nValue={nValue}
          sko={input.sko}
          skoSqr={skoSqr}
          generalDispersionDeviation={generalDispersionDeviation}
        />
        <DispersionEstimate
          modifier="small"
          tValue={input.tValue}
          nValue={nValue}
          sko={input.sko}
          skoSqr={skoSqr}
          generalDispersionDeviation={generalDispersionDeviation}
        />
        <DispersionEstimate
          modifier="very small"
          tValue={input.tValue}
          nValue={nValue}
          sko={input.sko}
          skoSqr={skoSqr}
          generalDispersionDeviation={generalDispersionDeviation}
        />
        <li></li>
        {/*  */}
        <p>
               {' '}
          {hypotAccept ? '' : ''}   {' '}
          {Letters.significanceLevel} = {input.significanceLevel},    
            {Letters.x0sqr} = {x0SqrValue} {hypotAccept ? '' : ''}{' '}
           
          {Letters.xSqr} = {input.xPirson}.    {confidenceLevel * 100}% 
              {(input.specX * 100 - generalDeviation * 100) / 100}  {' '}
          {(input.specX * 100 + generalDeviation * 100) / 100},    {' '}
          {confidenceLevel}    {' '}
          {(skoSqr * 100 - generalDispersionDeviation * 100) / 100} -{' '}
          {(skoSqr * 100 + generalDispersionDeviation * 100) / 100}.
        </p>
      </ol>
    </div>
  );
};

export default Part1;
